.modal__success-wrapper {
  padding: 39px 39px 30px 37px;
}

.modal__fail-wrapper {
  padding: 45px 42px 35px 41px;
}

.modal {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4;
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);

  &--success-closed,
  &--fail-closed {
    display: none;
  }
}

.modal__wrapper {
  position: relative;
  width: 320px;
  margin: auto;
  background-color: #ffffff;

  @media (min-width: @tablet-width) {
    width: 640px;
  }

  &--fail {
    @media (min-width: @tablet-width) {
      width: 400px;
    }
  }
}

.modal__button {
  margin: 0 auto;
  padding: 18px 20px 14px;
  font-size: 18px;
  line-height: 26px;

  &--success {
    min-width: 280px;
  }

  &--fail {
    min-width: 180px;
  }
}

.modal__wrapper-btn-success {
  padding: 20px;
  background-color: #f2f2f2;
}

.modal__title {
  margin: 0;
  margin-bottom: 28px;
}

.modal__subtitle {
  margin: 0;
  margin-bottom: 24px;
}
